interface QueuedFunc<T, P> {
  args?: P[],
  concurrency: number,
  func: (...args: any[]) => any,
  resolve: (val: T) => void,
}

export class StyQueue {
  constructor (defaultConcurrency = 5) {
    this.defaultConcurrency = defaultConcurrency;
    this.queue = [];
    this.working = 0;
  }
  defaultConcurrency: number;

  private queue: QueuedFunc<any, unknown>[] = [];
  private working: number;

  private queue2working = async () => {
    if (this.working < this.queue[0]?.concurrency) {
      const { args, func, resolve } = <QueuedFunc<any, unknown>> this.queue.shift();
      this.working++;
      resolve(await func(...args ?? []));
      this.working--;
      this.queue2working();
    }
  };

  get length () : number {
    return this.queue.length + this.working;
  }

  shift = () => this.queue.shift();
  pop = () => this.queue.pop();

  enqueue = <T, P extends Array<unknown>> (func: (...args: P) => Promise<T> | T, args?: P, concurrency = this.defaultConcurrency): Promise<T> => {
    const promise = new Promise<T>((resolve) => {
      this.queue.push({
        args,
        concurrency: concurrency < 1 ? 1 : concurrency,
        func,
        resolve,
      });
    });

    this.queue2working();
    return promise;
  };
}
